
@media screen and (min-width: 1200px) {
    .ProfileImage {
        width: 200px;
        height: 200px;
        border-radius: 100px;
        box-shadow: 0px 5px 14px -1px rgba(0,0,0,0.3);
    }
    
    .ProgrammingSect {
        display: block;
        
        padding: 10px;
    
        margin-top: 50px;
        margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
    
        border-style: solid;
        border-width: 1px;
        border-radius: 16px;
        border-color: lightgray;
    }
    
    .PhotographySect {
        display: block;
        
        padding: 10px;
    
        margin-top: 50px;
        margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
    
        border-style: solid;
        border-width: 1px;
        border-radius: 16px;
        border-color: lightgray;
    }
    
    .Gallery {
        display: grid;
    
        width: 90%;
        height: 90%;
    
        grid-template-columns: auto auto auto;
        grid-template-rows: auto auto auto;
    }
    
    .RepoSect {
        display: grid;
    
        grid-template-columns: 46% 46%;
        gap: 8%;
    
        width: 75%;
    
        padding: 0;
        margin: auto;
    }
}

